@import "config/themes/_blackjack.scss";

.root {
  padding: 20px 30px;
  @media (max-width: 500px) {
    padding: 5px;
  }
}

.currencyBlock {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  @media (max-width: 1200px) {
    flex-direction: column;
    align-items: inherit;
  }

  @media (max-width: 500px) {
    gap: 5px;
  }
}

.selectWrapper {
  width: 50%;
  height: 60px;
  & > div {
    height: 100%;
  }
  @media (max-width: 1200px) {
    width: 100%;
  }

  @media (max-width: 500px) {
    height: 40px;

    & img {
      height: 25px;
      width: auto;
    }
  }
}

.select {
}

.inputWrapper {
  width: 50%;
  position: relative;

  @media (max-width: 1200px) {
    width: 100%;
  }
}

.buttonsWrapper {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 20px;

  @media (max-width: 500px) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;

    & button {
      height: 35px;
      padding: 0;
      font-size: 16px;
    }
  }
}

.buttonsWrapperDisabled {
  display: none;
}

.betButton {
  background: linear-gradient(276.53deg, #2e2eed -93.34%, #4e4ef9 82.6%);
  box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.135107);
  color: white;
  min-width: 190px;
  height: 58px;
  font-size: 22px;
  border-radius: 8px;
  padding: 0;
  @media (max-width: 1350px) and (min-width: 1200px) {
    min-width: 140px;
  }

  @media (max-width: 500px) {
    height: 40px;
    font-size: 16px;
  }
}

.warning {
  top: -110px;
}

.insuranceWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  gap: 10px;
  color: #ffffff;
  font-size: 24px;
}

.insuranceWrapperDisabled {
  display: none;
}

.insuranceButtonsWrapper {
  display: flex;
  width: 100%;
  gap: 20px;
}
