@import "sass/index";

.root {
  position: relative;
  padding: 5px 10px;
  background: var(--template-gameCell-wrapper);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: flex;
  flex-direction: column;

  @media (max-width: 600px) {
    padding: 5px;
  }
}

.deck {
  position: absolute;
  display: flex;
  align-self: flex-end;
  top: 20%;
  height: 40%;

  & > img {
    width: auto;
    height: 100%;
  }
  @media (max-width: 600px) {
    height: 30%;
  }
}
