@import "config/themes/_blackjack.scss";

.wrapper {
  display: flex;
  align-items: flex-end;
  width: 100%;
  background: use(--template-anyContent-colors-third);
  border: 2px #2e2eed solid;
  height: 60px;
  position: relative;
  border-radius: 8px;
  & > div > label {
    line-height: 16px;
    font-size: 13px;
    font-family: "Varela Round";
    color: use(--template-statisticsBlock-tab-color) !important;
    opacity: 0.5;
    left: 20px;
    top: -10px;
  }
  @media (max-width: 1350px) and (min-width: 1200px) {
    & > div > label {
      left: 5px;
    }
  }
  & > div {
    margin-top: 0;
  }
  @media (max-width: 500px) {
    display: block;
    height: max-content;
    & > div > label {
      top: 5px;
      left: 11px;
    }
  }
}

.root {
  width: 100%;
  margin-top: 0;
  border: 0;
  background: transparent;
  border-radius: 8px;
  height: 40px;
  box-shadow: none !important;
  font-size: 18px;
  color: use(--template-statisticsBlock-tab-color);
  & input {
    padding-left: 10px;
    @media (max-width: 1350px) and (min-width: 1200px) {
      padding: 5px;
    }
  }
  @media (max-width: 500px) {
    padding-left: 20%;
    height: 25px;
    font-size: 14px;

    & input {
      text-align: end;
    }
  }
}

.disabled {
  color: rgba(#ffffff, 0.6);
}

.buttonsWrapper {
  display: flex;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  @media (max-width: 1350px) and (min-width: 1200px) {
    padding-left: 0;
    padding-right: 5px;
  }
}
