.button {
  cursor: pointer;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: 1.44px;
  text-transform: uppercase;
  border: none;
  background: #111320;
  box-shadow: -4px -4px 7px rgba(58, 68, 93, 0.4),
    4px 4px 7px rgba(6, 12, 24, 0.5);
  border-radius: 8px;
  width: 100%;
  height: 50px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;

  &:hover {
    background: #252836;
    box-shadow: -8px -8px 19px rgba(58, 68, 93, 0.3),
      4px 4px 7px rgba(6, 12, 24, 0.5);
  }
  &:active {
    background: #111320;
    box-shadow: 4px 4px 7px rgba(6, 12, 24, 0.5);
  }
  &:disabled {
    cursor: default;
    background: #1b1d2a;
    box-shadow: none;
    opacity: 0.5;
  }
}

.icon {
  cursor: inherit;
  padding: 0;
  width: 24px;
  height: 24px;
}
