@import "sass/index";

.root {
  width: 100%;
  font-weight: 500;
  margin-top: -16px;

  @media (max-width: 420px) {
    margin-top: 0;
  }

  & input {
    font-weight: 500;
  }
}

.optionsWrapper {
  background-color: use(--template-gameCell-controller);
  & > div {
    margin: 0;
  }
  padding-top: 10px;
  padding-bottom: 10px;
  & > div > button > span > svg path {
    fill: use(--template-button-option-color);
  }
}

.hrSmall {
  width: 100%;
  height: 1px;
  background-color: transparent;
}

.hrBig {
  width: 100%;
  height: 10px;
  background-color: transparent;
}

.statisticsWrapper {
  margin-top: 10px;
}
