.button {
  height: 100%;
  background-color: #2d2e3d;
  font-family: "Gilroy";
  color: rgba(#ffffff, 0.5);
  min-width: 0;
  max-width: 40px;
  border-radius: 6px;
  margin-left: 2px;
  @media (max-width: 500px) {
    position: relative;
    min-width: 25%;
    max-width: 25%;
    padding: 2.5px;
  }
}
